#modal {
  left: 50%;
  margin: -250px 0 0 -40%;
  opacity: 0;
  position: absolute;
  top: -50%;
  visibility: hidden;
  width: 80%;
  z-index: 100;
  box-sizing: border-box;
  transition: all .4s ease-in-out;
  -moz-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
}

.logo {
  height: 50px;
  width: 250px;
}

#modal:target {
  opacity: 1;
  top: 28%;
  visibility: visible;
}

@media (min-height: 480px) and (max-height: 550px) {
  #modal:target {
    top: 70%;
  }
}

@media (min-height: 640px) and (max-height: 800px) {
  #modal:target {
    top: 41.5%;
  }
}

@media (min-height: 1024px) and (max-height: 1180px) {
  #modal:target {
    top: 40%;
  }
}

@media (min-height: 1280px) and (max-height: 1480px) {
  #modal:target {
    top: 30%;
  }
}

#modal .header, #modal .footer {
  border-bottom: 1px solid #1ABC9C;
  border-radius: 5px 5px 0 0;
}

#modal .footer {
  border: none;
  border-top: 1px solid #e7e7e7;
  border-radius: 0 0 5px 5px;
}

#modal h2 {
  margin: 0;
  color: #fff;
}

#modal .btn {
  float: right;
}

#modal .copy, #modal .header, #modal .footer {
  padding: 16px;
  color: #fff;
}

.modal-content {
  position: relative;
  z-index: 20;
  border-radius: 5px;
  color: #fff;
}

#modal .copy {
  height: 100%;
}

#modal .overlay {
  background-color: #000;
  background: rgba(0, 0, 0, 0.8);
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}

.close {
  color: #d7d7d7;
  padding: 5px 16px;
  float: right;
  text-decoration: none;
}

.close:hover,
.close:focus {
  color: #eba900;
  text-decoration: none;
  cursor: pointer;
}

.sect {
  background-color: #d7d7d7;
  padding: 20px;
}

